.hero {
  height: 100vh;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .text {
    cursor: default;
    margin-left: 40px;
    width: 35vw;
    color: $whiteColor;
    background-color: rgb(70, 70, 70);
    border: 2px solid $blueColor;
    padding: 30px 25px;

    @media (max-width: $breakpoin1440) { 
     width: 50vw;
    }
    @media (max-width: $breakpoin768) {
      width: 60vw;
      margin-left: 0;
    }
    @media (max-width: $breakpoin480) {
      width: 100vw;
    }
    @media (max-width: 360px) {
      padding: 0 10px;
    }
    .sec {
      @media (max-width: 1024px) {
        display: none;
      }
    }

    h1{
      @media (max-width: $breakpoin480) { 
       font-size: calc(var(--mainFont + 15px));
      }

      @media (max-width: 360px) { 
       font-size: calc(var(--mainFont) - 3px);
      }
    }
  }
}

.first {
  background: url('../pics/hero.png');
  background-size: cover;
  background-position: 100%;

  @media (max-width: 1439px) {
    background-position: 80%;
  }
}
