body {
  background: $bodyColor;
  max-width: 100vw;
  position: relative;
  margin: 0;
  padding: 0;
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow-x: hidden;
  font-size: var(--mainFont);
  font-family: 'Roboto', sans-serif;
  transition: padding-left 300ms;
}

.locked {
  background-color: grey;
}

