.header {
  position: fixed;
  box-sizing: border-box;
  top: 0px;
  left: 50%;
  transform: translate(-50%);
  width: 100vw;
  height: 14vh;
  color: $whiteColor;
  display: flex;
  padding: 0 77px;
  justify-content: space-between;
  align-items: center;
  overflow-x: hidden;
  z-index: 10;

  @media (max-width: $breakpoin1024) {
    height: 150px;
  }
  @media (max-width: $breakpoin768) {
    padding: 10px 30px;
    height: 10vh;
  }
  @media (max-width: $breakpoin480) {
    justify-content: center;
    margin-top: 20px;
  }

  .logo-txt {
    cursor: pointer;
    background-color: $blueColor;
    border-radius: 5px;
    padding: 15px;
  }

  .number {
    background-color: $blueColor;
    border-radius: 5px;
    padding: 15px;
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    @media (max-width: $breakpoin480) {
      display: none;
    }
  }
}
